<template>
  <h2>My Lotto Number Generator</h2>
  <div class="country-tabs">
    <div class="tabs">
      <button
        v-for="(country, index) in countries"
        :key="index"
        @click="selectCountry(index)"
        :class="{'active-tab': selectedCountryIndex === index}"
      >
        <img :src="country.flag" :alt="country.name" class="flag"/>
        <span>{{ country.name }}</span>
      </button>

    </div>

    <div class="country-info" v-if="selectedCountry">
      <hr>
      <h2 class="lotto-title">{{ selectedCountry.lottoName }}</h2>
      <span>{{ selectedCountry.rules }}</span>
      <!-- <img :src="selectedCountry.flag" :alt="selectedCountry.name" class="flag-large"/> -->
    </div>
  </div>

  <!-- Dynamic component based on selected country -->
  <component :is="selectedCountryComponent"></component>

</template>

<script>
export default {
  data () {
    return {
      countries: [
        {
          name: 'United States',
          flag: 'https://flagcdn.com/us.svg',
          lottoName: 'Powerball',
          rules: 'Choose 5 numbers from 1 to 69 and 1 Powerball number from 1 to 26',
          code: 'USA'
        },
        {
          name: 'China',
          flag: 'https://flagcdn.com/cn.svg',
          lottoName: 'China Welfare Lottery',
          rules: 'Choose 6 numbers from 1 to 33 and 1 additional number from 1 to 16',
          code: 'CHN'
        },
        {
          name: 'South Korea',
          flag: 'https://flagcdn.com/kr.svg',
          lottoName: 'Lotto',
          rules: 'Choose 6 numbers from 1 to 45',
          code: 'KOR'
        },
        {
          name: 'Japan',
          flag: 'https://flagcdn.com/jp.svg',
          lottoName: 'Loto',
          rules: 'Choose 6 numbers from 1 to 43',
          code: 'JPN'
        },
        {
          name: 'Germany',
          flag: 'https://flagcdn.com/de.svg',
          lottoName: 'Lotto',
          rules: 'Choose 6 numbers from 1 to 49 and 1 Superzahl from 0 to 9',
          code: 'DEU'
        },
        {
          name: 'India',
          flag: 'https://flagcdn.com/in.svg',
          lottoName: 'Playwin',
          rules: 'Choose 6 numbers from 1 to 49',
          code: 'IND'
        },
        {
          name: 'United Kingdom',
          flag: 'https://flagcdn.com/gb.svg',
          lottoName: 'National Lottery',
          rules: 'Choose 6 numbers from 1 to 59',
          code: 'GBR'
        },
        {
          name: 'France',
          flag: 'https://flagcdn.com/fr.svg',
          lottoName: 'Loto',
          rules: 'Choose 5 numbers from 1 to 49 and 1 additional number from 1 to 10',
          code: 'FRA'
        },
        {
          name: 'Italy',
          flag: 'https://flagcdn.com/it.svg',
          lottoName: 'SuperEnalotto',
          rules: 'Choose 6 numbers from 1 to 90',
          code: 'ITA'
        },
        {
          name: 'Canada',
          flag: 'https://flagcdn.com/ca.svg',
          lottoName: 'Lotto 6/49',
          rules: 'Choose 6 numbers from 1 to 49',
          code: 'CAN'
        }

      ],
      selectedCountryIndex: 0,
      selectedCountryComponent: null
    }
  },
  computed: {
    selectedCountry () {
      return this.countries[this.selectedCountryIndex]
    }
  },
  mounted () {
    // Load the initial selected country component when the component is mounted
    this.loadSelectedCountryComponent()
  },
  watch: {
    selectedCountryIndex () {
      // Reload the selected country component when the index changes
      this.loadSelectedCountryComponent()
    }
  },
  methods: {
    selectCountry (index) {
      this.selectedCountryIndex = index
      console.log(`Selected country index changed to ${index}`)
    },
    loadSelectedCountryComponent () {
      const countryCode = this.countries[this.selectedCountryIndex].code
      import(`../components/LottoGenerator${countryCode}.vue`)
        .then(module => {
          this.selectedCountryComponent = module.default
          console.log(`Successfully imported component: LottoGenerator${countryCode}.vue`)
        })
        .catch(error => {
          console.error(`Error loading component for country ${countryCode}:`, error)
        })
    }
  }
}
</script>

<style>
.country-tabs {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tabs {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
}

.tabs button {
  border: none;
  background: none;
  cursor: pointer;
  margin: 5px;
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.tabs button.active-tab {
  background-color: #ddd;
}

.tabs img.flag {
  width: 30px;
  height: 20px;
  margin-right: 10px;
}

.country-info {
  text-align: center;
}

.flag-large {
  width: 100px;
  height: 60px;
}

h2.lotto-title {
  /* General Styling */
  font-family: 'Open Sans', sans-serif; /* Choose a clean, readable font */
  font-size: 3rem; /* Adjust font size for impact */
  text-align: center; /* Center the title for balance */
  margin: 10px 0; /* Add generous spacing above and below */

  /* Border Removal */
  border: none; /* Remove any default borders */
  box-shadow: 0 0 0 0 transparent; /* Remove box shadow (if present) */

  /* Text Gradient (Optional) */
  background-clip: text; /* Apply background to text only */
  background-image: linear-gradient(to right, #FFD700, #F57C00); /* Create a gradient */
  -webkit-text-fill-color: transparent; /* For Chrome */
  -webkit-background-clip: text; /* For Chrome */

  /* Animation (Optional) */
  animation: title-pulse 2s ease-in-out infinite alternate; /* Add a subtle pulse animation */
}

@keyframes title-pulse {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.05);
  }
}

</style>
