<template>
  <h1>QR Code Generator</h1>
  <div class="box-r">
    <div class="row-s4">
      <div class="tab-menu">
        <!-- 각 탭 버튼 -->
        <div class="btn-tab" :class="{ active: activeTab === 'link' }" @click="setTab('link')">
          <i class="fas fa-link"></i>
          <span>Link</span>
        </div>
        <div class="btn-tab" :class="{ active: activeTab === 'text' }" @click="setTab('text')">
          <i class="fas fa-align-justify"></i>
          <span>Text</span>
        </div>
        <div class="btn-tab" :class="{ active: activeTab === 'phone' }" @click="setTab('phone')">
          <i class="fas fa-phone"></i>
          <span>Call</span>
        </div>
        <div class="btn-tab" :class="{ active: activeTab === 'sms' }" @click="setTab('sms')">
          <i class="fas fa-sms"></i>
          <span>SMS</span>
        </div>
        <div class="btn-tab" :class="{ active: activeTab === 'email' }" @click="setTab('email')">
          <i class="fas fa-envelope"></i>
          <span>E-mail</span>
        </div>
        <div class="btn-tab" :class="{ active: activeTab === 'wifi' }" @click="setTab('wifi')">
          <i class="fas fa-wifi"></i>
          <span>WI-FI</span>
        </div>
      </div>

      <!-- 각 탭 별 내용 -->
      <div class="tab-sub" v-if="activeTab === 'link'">
        <p class="text-s1">URL</p>
        <input class="input-qr" v-model="urlValue" @input="updateQrValue('link')" placeholder="URL here" />
        <p class="text-s3">Your QR code will open this URL. e.g., https://qrfont.github.io</p>
      </div>
      <div class="tab-sub" v-if="activeTab === 'text'">
        <p class="text-s1">Text</p>
        <textarea class="input-text" v-model="textValue" @input="updateQrValue('text')" placeholder="text here"></textarea>
        <p class="text-s3">Your QR code will encode this text.</p>
      </div>
      <div class="tab-sub" v-if="activeTab === 'phone'">
        <p class="text-s1">Phone Number</p>
        <div class="align-hor">
        <select class="input-select"  v-model="countryValue" @change="updateQrValue('phone')">
          <CountryCode></CountryCode>
          <!-- <option value="+1">United States (+1)</option>
          <option value="+82">South Korea (+82)</option>
          <option value="+81">Japan (+81)</option> -->
        </select>
        <input class="input-qr" v-model="phoneValue" @input="updateQrValue('phone')" placeholder="phone number here" />
        </div>
        <p class="text-s3">Your QR code will call this number.</p>
      </div>
      <div class="tab-sub" v-if="activeTab === 'sms'">
        <p class="text-s1">Phone Number</p>
        <div class="align-hor">
          <select class="input-select"  v-model="countryValue" @change="updateQrValue('sms')">
          <CountryCode></CountryCode>
          <!-- <option value="+1">United States (+1)</option>
          <option value="+82">South Korea (+82)</option>
          <option value="+81">Japan (+81)</option> -->
        </select>
        <input class="input-qr" v-model="phoneValue" @input="updateQrValue('sms')" placeholder="phone number here" />
        </div>
        <p class="text-s1">SMS Message</p>
        <textarea class="input-text" v-model="smsValue" @input="updateQrValue('sms')" placeholder="SMS here" />
        <p class="text-s3">Your QR code will send this SMS message.</p>
      </div>
      <div class="tab-sub" v-if="activeTab === 'email'">
        <p class="text-s1">E-mail</p>
        <input class="input-qr" v-model="emailValue" @input="updateQrValue('email')" placeholder="e-mail here" />
        <input class="input-qr" v-model="emailSubjectValue" @input="updateQrValue('email')" placeholder="subject here" />
        <textarea class="input-text" v-model="emailMessageValue" @input="updateQrValue('email')" placeholder="message here"></textarea>
        <p class="text-s3">Your QR code will send an e-mail to this address.</p>
      </div>
      <div class="tab-sub" v-if="activeTab === 'wifi'">
        <p class="text-s1">WI-FI Information</p>
        <div class="align-hor">
          <input class="input-qr" style="width:400px" v-model="ssidValue" @input="updateQrValue('wifi')" placeholder="Wi-Fi SSID" />
          <select class="input-select" style="width:200px"  v-model="networkTypeValue" @change="updateQrValue('wifi')">
            <option value="WPA">WPA/WPA2</option>
            <option value="WEP">WEP</option>
            <option value="">No encryption</option>
          </select>
        </div>
        <div class="align-hor">
          <input class="input-qr"  style="width:300px"  v-model="wifiPasswordValue" @input="updateQrValue('wifi')" placeholder="Wi-Fi password" />
          <label>
            <input type="checkbox" v-model="hiddenValue" @change="updateQrValue('wifi')" /> Hidden
          </label>
        </div>
        <p class="text-s3">Your QR code will connect to this WI-FI network.</p>
      </div>
    </div>
    <div class="row-s1">
      <!-- QR 코드 출력 -->
      <vue-qrcode
        :value="qrValue"
        :options="{
          width: 300,
          color: {
            dark: '#000000',
            light: '#ffffff',
          },
        }"
      ></vue-qrcode>
      <!-- QR 코드 다운로드 및 인쇄 버튼 -->
      <p style="display: flex;justify-content: center;">
        <button class="button down" @click="downloadQRCode">DOWN</button>
        <button class="button down" @click="printQRCode">PRINT</button>
      </p>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import VueQrcode from '@chenfengyuan/vue-qrcode'
import CountryCode from '../components/CountryCode.vue'

export default defineComponent({
  name: 'QRCodeGenerator',
  components: {
    VueQrcode,
    CountryCode
  },
  setup () {
  // 반응형 변수 선언
    const urlValue = ref('')
    const textValue = ref('')
    const countryValue = ref('')
    const phoneValue = ref('')
    const smsValue = ref('')
    const emailValue = ref('')
    const emailSubjectValue = ref('')
    const emailMessageValue = ref('')
    const ssidValue = ref('')
    const networkTypeValue = ref('')
    const wifiPasswordValue = ref('')
    const hiddenValue = ref(false)
    const qrValue = ref('https://qrfont.github.io/')

    const activeTab = ref('link') // 기본적으로 활성화된 탭

    // 탭 설정 메소드
    const setTab = (tab) => {
      activeTab.value = tab
      updateQrValue(tab)
    }

    // QR 코드 값 업데이트 메소드
    const updateQrValue = (type) => {
      switch (type) {
        case 'link':
          qrValue.value = urlValue.value || null
          break
        case 'text':
          qrValue.value = textValue.value || null
          break
        case 'phone':
          qrValue.value = phoneValue.value ? `tel:${countryValue.value}${phoneValue.value}` : null
          break
        case 'sms':
          qrValue.value = phoneValue.value ? `sms:${countryValue.value}${phoneValue.value}?body=${encodeURIComponent(smsValue.value)}` : null
          break
        case 'email':
          qrValue.value = emailValue.value ? `mailto:${emailValue.value}?subject=${encodeURIComponent(emailSubjectValue.value)}&body=${encodeURIComponent(emailMessageValue.value)}` : null
          break
        case 'wifi':
        // qrValue.value = wifiValue.value || null
          qrValue.value = `WIFI:T:${networkTypeValue.value};S:${ssidValue.value};P:${wifiPasswordValue.value};H:${hiddenValue.value ? 'true' : 'false'};;`
          break
        default:
          qrValue.value = null
      }
    }

    // QR 코드 이미지 다운로드 함수
    const downloadQRCode = () => {
      const canvas = document.querySelector('canvas')
      const url = canvas.toDataURL('image/png')
      const a = document.createElement('a')
      a.href = url
      a.download = 'qrcode.png'
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a)
    }

    // QR 코드 이미지 인쇄 함수
    const printQRCode = () => {
      const canvas = document.querySelector('canvas') // vue-qrcode 컴포넌트의 캔버스 요소 선택
      const url = canvas.toDataURL('image/png') // PNG 형식으로 이미지 데이터 URL 생성

      // 새로운 윈도우 창 열기
      const popupWin = window.open('', '_blank')
      popupWin.document.open()
      popupWin.document.write(`
      <html>
        <head>
          <title>Print QR Code</title>
          <style>
            body { margin: 0; }
            img { display: block; margin: 0 auto; }
          </style>
        </head>
        <body>
          <img src="${url}" style="max-width: 100%; max-height: 100%;" />
        </body>
      </html>
    `)
      popupWin.document.close()

      // 이미지가 로드된 후 프린트 요청
      popupWin.onload = function () {
        popupWin.print()
      }
    }

    return {
    // 외부로 내보내는 변수 및 메소드들
      urlValue,
      textValue,
      countryValue,
      phoneValue,
      smsValue,
      emailValue,
      emailSubjectValue,
      emailMessageValue,
      ssidValue,
      networkTypeValue,
      wifiPasswordValue,
      hiddenValue,
      qrValue,
      activeTab,
      setTab,
      updateQrValue,
      downloadQRCode,
      printQRCode
    }
  }
})
</script>

